export default () => {
  if (!document.querySelector(".footer-top")) {
    var footer_top_el = document.createElement("div");
    if (document.querySelector("#footer")) {
      document
        .querySelector("#footer")
        .insertBefore(
          footer_top_el,
          document.querySelector("#footer").firstChild
        );

      footer_top_el.outerHTML = `
      <div class="footer-top wow fadeInUp animated" data-wow-delay="100ms" style="visibility: visible; animation-delay: 100ms; animation-name: fadeInUp;">
      <div class="container">
          <div class="footer-policy row">
              <div class="col-lg-3 col-md-3 col-sm-3 col-ms-6 col-xs-12">
                <i class="fa fa-globe"></i>
              
                <span>Global Selling</span>
              </div>
              <div class="col-lg-3 col-md-3 col-sm-3 col-ms-6 col-xs-12">
                 <i class="fa fa-product-hunt" style="display:inline-block" aria-hidden="true"></i>
                <span>Reliable Quality</span>
              </div>
              <div class="col-lg-3 col-md-3 col-sm-3 col-ms-6 col-xs-12">
           <i class="fa fa-object-ungroup"></i>
                <span>Professional Design</span>
              </div>
              <div class="col-lg-3 col-md-3 col-sm-3 col-ms-6 col-xs-12">
           <i class="fa fa-truck"></i>
                <span>On Time Delivery</span>
              </div>
          </div>
        </div>
    </div>
    `;
    }
  }
};
