import React from "react";

import styled from "styled-components";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

class SimpleSlider extends React.Component {
  render() {
    var settings = {
      // dots: true,
      autoplay: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
    return (
      <Slider {...settings}>
        <div>
          <a href="https://allenjoystudio.com/products/allenjoy-custom-backdrop-dlz-cus-001">
            <img
              src={
                "https://cdn.shopifycdn.net/s/files/1/2443/0381/files/202014395225005.jpg?v=1591952160"
              }
              width="100%"
            ></img>
          </a>
        </div>
        <div>
          <a href="https://allenjoystudio.com/products/allenjoy-custom-round-backdrop-dlz-cus-003">
            <img
              src={
                "https://cdn.shopifycdn.net/s/files/1/2443/0381/files/1591951270.png?v=1591952449"
              }
              width="100%"
            ></img>
          </a>
        </div>
      </Slider>
    );
  }
}

const PartOne = (props) => {
  return (
    <>
      <SimpleSlider></SimpleSlider>
    </>
  );
};

export default PartOne;
