export default () => {
  //this removes liquid error text and powered by shopify text on page
  var nodes = document.evaluate(
    "//div[contains(., 'Liquid error:')]/text()",
    document.body,
    null,
    XPathResult.ORDERED_NODE_SNAPSHOT_TYPE,
    null
  );

  for (var i = 0; i < nodes.snapshotLength; i++) {
    // console.log(nodes.snapshotItem(i).textContent);
    if (nodes.snapshotItem(i).textContent.includes("Liquid error:")) {
      nodes.snapshotItem(i).textContent = "";
    }
  }

  nodes = document.evaluate(
    "//text()",
    document.body,
    null,
    XPathResult.ORDERED_NODE_SNAPSHOT_TYPE,
    null
  );

  for (var i = 0; i < nodes.snapshotLength; i++) {
    if (/Powered\s+by\s+Shopify/.test(nodes.snapshotItem(i).textContent)) {
      nodes.snapshotItem(i).textContent = "";
    }
  }
  if (document.body.querySelector(".f_bot .f_bot_left")) {
    nodes = document.evaluate(
      "//text()",
      document.body.querySelector(".f_bot .f_bot_left"),
      null,
      XPathResult.ORDERED_NODE_SNAPSHOT_TYPE,
      null
    );

    console.info("finding |");
    var nodesArray = Array(nodes.snapshotLength)
      .fill(0)
      .map((_, i) => nodes.snapshotItem(i))
      .map((e) => {
        // console.log(e.textContent);
        return e;
      })
      .filter((e) => e.textContent === " | ")
      .forEach((e) => (e.textContent = ""));
  }
};
