import React from "react";
import ReactDOM from "react-dom";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(6),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

const Shield = styled.span`
  width: 60px;
  background: #dc3545;
  height: 60px;
  color: #fff;
  font-size: 18px;
  text-align: center;
  line-height: 55px !important;
  border-bottom-left-radius: 35px;
  border-bottom-right-radius: 35px;
  display: inline-block;
`;

const PartTwo = (props) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={6} justify="center">
        <Grid item sm>
          <Paper className={classes.paper} elevation={0}>
            <Shield>1</Shield>
            <Typography variant="h3">DESIGN</Typography>
            <p>
              Send us your artwork or ideas for a backdrop. Our design team will
              create an amazing backdrop for your event.
            </p>
          </Paper>
        </Grid>
        <Grid item sm>
          <Paper className={classes.paper} elevation={0}>
            <Shield>2</Shield>
            <Typography variant="h3">ORDER</Typography>
            <p>
              After confirming the design , we will send you a purchase
              link.Once ordered the printing process begins.
            </p>
          </Paper>
        </Grid>
        <Grid item sm>
          <Paper className={classes.paper} elevation={0}>
            <Shield>3</Shield>
            <Typography variant="h3">SHIP</Typography>
            <p>
              Printing and packing will take 2-3 working days.Don't know
              delivery time. Ask us about shipping method and time.
            </p>
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};

export default PartTwo;
