import React from "react";
import ReactDOM from "react-dom";
import styled from "styled-components";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import CheckIcon from "@material-ui/icons/Check";
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";
import { pink } from "@material-ui/core/colors";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { withStyles, makeStyles } from "@material-ui/core/styles";

document.head.innerHTML +=
  '<link href="http://fonts.googleapis.com/css?family=Great+Vibes" rel="stylesheet">';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(0),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  customWidth: {
    maxWidth: 480,
  },
}));

const HtmlTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: "#f5f5f9",
    color: "rgba(0, 0, 0, 0.87)",
    maxWidth: 480,
    fontSize: theme.typography.pxToRem(12),
    border: "1px solid #dadde9",
  },
}))(Tooltip);

// const theme = createMuiTheme();

// theme.typography.h3 = {
//   lineHeight: 2,
// };
// theme.typography.h4 = {
//   lineHeight: 2,
// };
const Heading = styled.h2`
  font-size: 2.8em;
  text-transform: capitalize;
  text-align: left;
  color: #000;
  font-family: "Great Vibes", cursive;
  font-weight: 500;
  margin-bottom: 20px;
`;

const PictureMore = () => (
  <>
    <Typography variant="h4">
      Print Your Personalized Picture Directly
    </Typography>
    <Typography variant="h6">
      Please provide the printable image with 300DPI (.jpg file) or Vector
      file(.AI/SVG).
    </Typography>
    <br></br>
    <Typography variant="h4"> Custom Personalized Backdrops</Typography>

    <Typography variant="h6">
      If you want to custom the backdrop that from our design Please provide us
      the product link or the image of the product in our store Or on Amazon,
      AliExpress platform. If not,please provide the sample pictures of your
      ideas
    </Typography>
  </>
);

const PinkCheck = () => (
  <CheckIcon
    style={{ color: pink[500], verticalAlign: "middle" }}
    fontSize="large"
  ></CheckIcon>
);
const PartThree = (props) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container /* spacing={6} */ /* justify={"space-around"} */>
        <Grid item sm>
          <Paper
            className={classes.paper}
            elevation={0}
            style={{ textAlign: "center" }}
          >
            <img src="//cdn.shopifycdn.net/s/files/1/2443/0381/files/s3_large.png?v=1591940948"></img>
          </Paper>
        </Grid>
        <Grid item sm>
          <Paper className={classes.paper} elevation={0}>
            <Heading>Everything you need to prodive</Heading>
            {/* <ThemeProvider theme={theme}> */}
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Picture
              <HtmlTooltip
                title={<PictureMore></PictureMore>}
                // classes={{ tooltip: classes.customWidth }}
              >
                <Button>More</Button>
              </HtmlTooltip>
            </Typography>

            <Typography variant="h4">
              <PinkCheck></PinkCheck> SizeWidth X Height by default
            </Typography>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Material{" "}
              <HtmlTooltip
                style={{ maxWidth: "none" }}
                title={
                  <img
                    style={{ width: "100%" }}
                    src="https://cdn.shopifycdn.net/s/files/1/2443/0381/files/events-custom-page-various-styles_large.png?v=1591953383"
                  ></img>
                }
                placement="top-start"
              >
                <Button>More</Button>
              </HtmlTooltip>
            </Typography>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Customization Requirements
            </Typography>
            <Typography variant="h4">
              <PinkCheck></PinkCheck> Date of your events
            </Typography>
            {/* </ThemeProvider> */}
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
};

export default PartThree;
